
import VueRouter from "vue-router";
import Login from '../views/Login';
import ErrorView from '../views/Error';
// import exam from "_exam/pc/exam/ExamInit";
Vue.use(VueRouter);

const routes = [
  //注入平台路由
  ...$platformRouter.pc,
  {
    path: "/pc/login",
    meta: {
      title: "登录",
      needLogin: false
    },
    component: Login
  },
  // {
  //   path: "/pc/exam",
  //   meta: {
  //     title: "考试",
  //     needLogin: false
  //   },
  //   component: exam
  // },
  //强制匹配404页面
  {
    path: "*",
    component: ErrorView
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

dsf.init.initRouter(router, 'pc');

export default router;