<template>
  <div class="full-view">
    <dsf-home v-if="pageSuffix == 'index'" :menu-list="menuList">
      <dsf-default-home-header slot="header" :dropdown-config="dropdownConfig" @dropdown-click="dropdownClick" />
      <dsf-default-home-aside-menu slot="left" position="left" />
      <router-view fit style="min-height:100%"></router-view>
      <dsf-personalization :show.sync="personalizationShow"></dsf-personalization>
    </dsf-home>
    <dsf-home v-else-if="pageSuffix == 'index1'" :menu-list="menuList">
      <dsf-template1-home-header slot="header" :dropdown-config="dropdownConfig" last-level-click @dropdown-click="dropdownClick" />
      <dsf-default-home-breadcrumb slot="breadcrumb" />
      <dsf-default-home-footer slot="footer" text="技术支持：上海梦创双杨数据科技股份有限公司" />
      <router-view fit></router-view>
      <dsf-personalization :show.sync="personalizationShow"></dsf-personalization>
    </dsf-home>
    <router-view v-else class="page-head-font-fixed"></router-view>
  </div>
</template>

<script>
export default {
  name: "Home",
  props: {
    pageSuffix: {
      type: String,
      default: 'else',
    }
  },
  data() {
    return {
      personalizationShow: false,
      /**
       * **用户头像下的菜单** <br/>
       *  **name**: {String} _必须_ 显示文字 <br/>
       *  **key**: {String | Number} _非必须_ 菜单标识<br/>
       *  -----以下属性任意存在一个------ <br/>
       *  **url**: {String} _非必须_ 如果存在则跳转页面 <br/>
       *  **openUrl**: {String} _非必须_ 如果存在则新开页面 <br/>
       *  **handler**: {String} _非必须_ 如果存在则调用对应方法
       */
      dropdownConfig: [
        // { name: '个人信息', key: 'personInfo', url: '' },
        // { name: '我的收藏', key: 'myCollect' },
        // { name: '我的红色慕课', key: 'myClass' },
        // { name: '我的积分', key: 'myIntegral' },
        // { name: '修改密码', key: 'alterPassword' },
        // { name: '切换为学员', key: 'changeRole' },
        { name: "个性化设置", key: "personalization", handler: "personalizationSetting" },
        { name: "退出", key: "loginOut", handler: "loginOut" }
      ],
      menuList: []
    };
  },
  created() {
    if (this.pageSuffix != 'else') {
      getMenuTree.call(this);
    }
  },
  methods: {
    // 用户头像下的菜单点击
    dropdownClick(item) {
      if (item.url) {
        window.location.href = item.url;
      } else if (item.openUrl) {
        window.open(item.openUrl);
      } else if (item.handler) {
        this[item.handler] && this[item.handler](item);
      } else {
        // 其他情况自行处理
      }
    },
    personalizationSetting() {
      this.personalizationShow = true;
    },
    // 自带方法：退出登录
    loginOut() {
      dsf.layer
        .confirm("您确定要退出吗?")
        .then(() => {
          // 应后端要求，退出登录cookies由后端清除，若接口异常直接提示报错信息
          this.$webAPI
            .loginOut()
            .then(({ success, message }) => {
              if (success) {
                dsf.storage.session.clear();
                location.replace(dsf.config.setting_public_pc_login_page);
              } else {
                dsf.layer.message(message, false);
              }
            })
            .catch(err => {
              console.error(err);
              dsf.layer.message("退出失败", false);
            });
        })
        .catch(() => {});
    }
  }
};

/**
 * 请求菜单数据
 */
function getMenuTree() {
  const homeMenu = dsf.storage.session.get("homeMenu");
  if (homeMenu) {
    this.menuList = homeMenu;
  } else {
    this.$http
      .get(dsf.config.setting_public_home_menu_src)
      .done(({ success, data }) => {
        if (success) {
          this.menuList = data;
          dsf.storage.session.set("homeMenu", data);
        }
      })
      .error((error) => {
        dsf.httpError(error)
        // dsf.layer.message(message, false);
      });
  }
}
</script>
